(() => {
  const refs = {
    openModalBtn: document.querySelector('[data-registration-modal-open]'),
    closeModalBtn: document.querySelector('[data-registration-modal-close]'),
    modal: document.querySelector('[data-modal-registration]'),
  };

  refs.openModalBtn.addEventListener('click', toggleModal);
  refs.closeModalBtn.addEventListener('click', toggleModal);

  function toggleModal() {
    refs.modal.classList.toggle('is-hidden');
  }
})();

export { toggleModal };
